.colorPicker {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 0 15px!important;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;
}

.colorPicker::-webkit-scrollbar {
    display: none;
}

.colorItem {
    display: block;
    scroll-snap-align: start;
    height: 30px;
    min-width: 30px;
    opacity: .4;
    transition: opacity .3s;
    margin-right: 15px;
    border-radius: 6px;
    animation: widen-in-color-item .3s;
}

@keyframes widen-in-color-item {
    from {
        width: 0;
    }
    to {
        width: 30px;
    }
}

.colorItem_active {
    composes: colorItem;
    opacity: 1;
}